const config = {
  siteTitle: "Teach Myself", // Site title.
  siteTitleShort: "Tom", // Short site title for homescreen (PWA). Preferably should be under 12 characters to prevent truncation.
  siteTitleAlt: "Thomas Chen journal blog", // Alternative site title for SEO.
  siteLogo: "/logos/logo-1024.png", // Logo used for SEO and manifest.
  siteUrl: "https://foxnewsnetwork.github.io", // Domain of your website without pathPrefix.
  pathPrefix: "/teach-myself", // Prefixes all links. For cases when deployed to example.github.io/gatsby-material-starter/.
  fixedFooter: false, // Whether the footer component is fixed, i.e. always visible
  siteDescription: "Thomas Chen's blog on machine learning, programming, physics, engineering, and other things built on GatsbyJS stater with Material design in mind.", // Website description used for RSS feeds/meta description tag.
  siteRss: "/rss.xml", // Path to the RSS file.
  siteFBAppID: "@TODO Add FB", // FB Application ID for using app insights
  siteGATrackingID: "@TODO Add GA", // Tracking code ID for google analytics.
  disqusShortname: "https-foxnewsnetwork-github-io-teach-myself", // Disqus shortname.
  postDefaultCategoryID: "Tech", // Default category for posts.
  dateFromFormat: "YYYY-MM-DD", // Date format used in the frontmatter.
  dateFormat: "DD/MM/YYYY", // Date format for display.
  userName: "Thomas Chen", // Username to display in the author segment.
  userEmail: "foxnewsnetworkr@example.com", // Email used for RSS feed's author segment
  userTwitter: "@badtastes", // Optionally renders "Follow Me" in the UserInfo segment.
  userLocation: "Aliso Viejo, California", // User location to display in the author segment.
  userAvatar: "https://api.adorable.io/avatars/150/test.png", // User avatar to display in the author segment.
  // User description to display in the author segment.
  userDescription: "Should have been an academic, but instead got suckered into becoming a software engineer. Now, slowly working my own way back. No, I do not watch Fox News.",
  // Links to social profiles/projects you want to display in the author segment/navigation bar.
  userLinks: [
    {
      label: "GitHub",
      url: "https://github.com/foxnewsnetwork/teach-myself",
      iconClassName: "fa fa-github"
    },
    {
      label: "Twitter",
      url: "https://twitter.com/badtastes",
      iconClassName: "fa fa-twitter"
    },
    {
      label: "Email",
      url: "mailto:foxnewsnetwork@gmail.com",
      iconClassName: "fa fa-envelope"
    }
  ],
  copyright: "Copyright Â© 2019. Thomas CHen" // Copyright string for the footer of the website and RSS feed.
};

// Validate

// Make sure pathPrefix is empty if not needed
if (config.pathPrefix === "/") {
  config.pathPrefix = "";
} else {
  // Make sure pathPrefix only contains the first forward slash
  config.pathPrefix = `/${config.pathPrefix.replace(/^\/|\/$/g, "")}`;
}

// Make sure siteUrl doesn't have an ending forward slash
if (config.siteUrl.substr(-1) === "/")
  config.siteUrl = config.siteUrl.slice(0, -1);

// Make sure siteRss has a starting forward slash
if (config.siteRss && config.siteRss[0] !== "/")
  config.siteRss = `/${config.siteRss}`;

module.exports = config;
